//初始化
(function () {
    let pageW = document.documentElement.offsetWidth * 0.75;

    var mW = pageW - 10;
    var mH = pageW;
    var mColorPolygon = '#000000'; //多边形颜色
    var mColorLines = '#000000'; //顶点连线颜色
    var mColorText = '#261a06';

    var mCenter = mW / 2; //中心点
    var mRadius = mCenter - 50; //半径(减去的值用于给绘制的文本留空间)


    window.redar = function () {

        console.log('yearData', yearData);
        let yearL = [];

        for(let o in yearData){
            yearL.push({sNum:yearData[o],yearIn:o});
        }
        // yearL.push(window.yearData['80'])
        // yearL.push(window.yearData['90'])
        // yearL.push(window.yearData['00'])
        // yearL.push(window.yearData['10'])

        window.yearData['80'] = 0;
        window.yearData['90'] = 0;
        window.yearData['00'] = 0;
        window.yearData['10'] = 0;

        yearL = yearL.sort(function (a, b) {
            return a.sNum - b.sNum;
        });

        console.log('yearL',yearL);
        if(yearL[yearL.length-1]){
            for(var i =0;i<yearL.length;i++){
                if(i == (yearL.length-1)){
                    yearData[yearL[yearL.length-1].yearIn] = 100;
                }else{
                    yearData[yearL[i].yearIn] = yearL[i].sNum / yearL[yearL.length-1].sNum * 100;
                }
                
            }
        }
        

        // if (yearL[yearL.length - 1] < 10) {
        // window.yearData['80'] = window.yearData['80'] * 10;
        // window.yearData['90'] = window.yearData['90'] * 10;
        // window.yearData['00'] = window.yearData['00'] * 10;
        // window.yearData['10'] = window.yearData['10'] * 10;
        // }
        // window.yearData['80'] = window.yearData['80'] * 10;
        // window.yearData['90'] = window.yearData['90'] * 10;
        // window.yearData['00'] = window.yearData['00'] * 10;
        // window.yearData['10'] = window.yearData['10'] * 10;

        var mData = [
            ['80年代', window.yearData['80']],
            ['90年代', window.yearData['90']],
            ['00年代', window.yearData['00']],
            ['10年代', window.yearData['10']]
        ];
        // var mData = [
        //     ['80年代', 70],
        //     ['90年代', 80],
        //     ['00年代', 90],
        //     ['10年代', 100]
        // ];
        var mCount = mData.length; //边数 
        var mAngle = Math.PI * 2 / mCount; //角度
        debugger;

        var mCtx = null;

        var canvas = document.createElement('canvas');
        // document.body.appendChild(canvas);
        $('.redar_box').html(canvas);
        $('.redar_box').append('<div class="tiptext">'+canvasText+'</div>');
    
        canvas.height = mH;
        canvas.width = mW;
        mCtx = canvas.getContext('2d');
        drawPolygon(mCtx);
        drawLines(mCtx);

        drawRegion(mCtx);
        drawText(mCtx);
        // drawCircle(mCtx);


        // 绘制多边形边
        function drawPolygon(ctx) {
            ctx.save();

            ctx.strokeStyle = mColorPolygon;
            var r = mRadius / mCount; //单位半径
            //画6个圈
            // for (var i = 0; i < mCount; i++) {
            //     ctx.beginPath();
            //     var currR = r * (i + 1); //当前半径
            //     //画6条边
            //     for (var j = 0; j < mCount; j++) {
            //         var x = mCenter + currR * Math.cos(mAngle * j);
            //         var y = mCenter + currR * Math.sin(mAngle * j);

            //         ctx.lineTo(x, y);
            //     }
            //     ctx.closePath()
            //     ctx.stroke();
            // }

            for (var i = 0; i < 4; i++) {
                ctx.beginPath();
                console.log(mCenter / (5 - i))
                let xStr = parseInt(mCenter * 0.65)
                console.log('xx' + xStr / (i + 1));
                // ctx.arc(mCenter, mH / 2, mCenter * 1.1 / (4 - i * 0.7) + mCenter * 0.05, 0, 2 * Math.PI);
                ctx.arc(mCenter, mCenter, xStr - i * 20, 0, 2 * Math.PI);
                console.log(mCenter * 1.1 / (4 - i * 0.7) + mCenter * 0.05)
                ctx.fillStyle = 'rgba(161,157,141,' + ((i + 1) / 10 + 0.3) + ')';
                console.log(i / 10 + 'ss');
                ctx.fill();
                ctx.stroke();
            }



            ctx.restore();
        }

        //顶点连线
        function drawLines(ctx) {
            ctx.save();
            console.log(mAngle);
            ctx.beginPath();
            ctx.strokeStyle = mColorLines;

            for (var i = 0; i < mCount; i++) {
                var x = mCenter + mRadius * Math.cos(mAngle * i + 0.8);
                var y = mCenter + mRadius * Math.sin(mAngle * i + 0.8);

                ctx.moveTo(mCenter, mCenter);
                ctx.lineTo(x, y);
            }

            ctx.stroke();

            ctx.restore();
        }

        //绘制文本
        function drawText(ctx) {
            ctx.save();

            var fontSize = mCenter / 8;
            ctx.font = fontSize + 'px Microsoft Yahei';
            ctx.fillStyle = mColorText;

            for (var i = 0; i < mCount; i++) {
                // console.log(i);
                var x = mCenter + mRadius * Math.cos(mAngle * i + 0.8);
                var y = mCenter + mRadius * Math.sin(mAngle * i + 0.8);

                if (mAngle * i + 0.8 >= 0 && mAngle * i + 0.8 <= Math.PI / 2) {
                    ctx.fillText(mData[i][0], x - fontSize * 2 + 45, y + fontSize * 0.5);
                } else if (mAngle * i + 0.8 > Math.PI / 2 && mAngle * i + 0.8 <= Math.PI) {
                    ctx.fillText(mData[i][0], x - ctx.measureText(mData[i][0]).width - 40 + fontSize * 2, y + fontSize * 0.5);
                } else if (mAngle * i + 0.8 > Math.PI && mAngle * i + 0.8 <= Math.PI * 3 / 2) {
                    ctx.fillText(mData[i][0], x - ctx.measureText(mData[i][0]).width - 25 + fontSize * 1, y + fontSize * 0.5);
                } else {
                    ctx.fillText(mData[i][0], x, y);
                }

            }

            ctx.restore();
        }



        //画点
        function drawCircle(ctx) {
            ctx.save();

            var r = mCenter / 18;
            for (var i = 0; i < mCount; i++) {
                var x = mCenter + mRadius * Math.cos(mAngle * i + 0.8) * mData[i][1] / 100;
                var y = mCenter + mRadius * Math.sin(mAngle * i + 0.8) * mData[i][1] / 100;

                ctx.beginPath();
                ctx.arc(x, y, r, 0, Math.PI * 2);
                ctx.fillStyle = 'rgba(0, 0, 0, 0.7)';
                ctx.fill();
            }

            ctx.restore();
        }

        //绘制数据区域
        function drawRegion(ctx) {
            ctx.save();

            ctx.beginPath();
            for (var i = 0; i < mCount; i++) {
                var x = mCenter + mRadius * Math.cos(mAngle * i + 0.8) * mData[i][1] / 100;
                var y = mCenter + mRadius * Math.sin(mAngle * i + 0.8) * mData[i][1] / 100;

                ctx.lineTo(x, y);
            }
            ctx.closePath();
            ctx.fillStyle = 'rgba(0, 0, 0, 0.7)';
            ctx.fill();

            ctx.restore();
        }



        //歌单

        function split_array(arr, len) {
            var a_len = arr.length;
            var result = [];
            for (var i = 0; i < a_len; i += len) {
                result.push(arr.slice(i, i + len));
            }
            return result;
        }


        if (window.sSheet.length > 0) {

            $('.redar_box').click(function () {
                $('.song_sheet_warp').show();
            })

            $('.song_sheet_warp .mask,.song_sheet_warp .slclose').click(function () {
                $('.song_sheet_warp').hide();
            })



            let pageData = split_array(window.sSheet, 10);

            let currentPage = 0;

            function setData(arr) {
                console.log(arr);
                let str = '';

                for (var i = 0; i < arr.length; i++) {

                    str += '<div class="item_s">' + arr[i][2].slice(0, arr[i][2].indexOf('/')) + '《' + arr[i][3] + '》</div>';

                }

                $('.song_content').html(str);
            }
            // debugger;
            setData(pageData[currentPage]);

            $('.pr').click(function () {
                currentPage--;
                if (currentPage <= 0) {
                    currentPage = 0;
                }
                setData(pageData[currentPage]);
            });

            $('.next').click(function () {
                currentPage++;
                if (currentPage >= (pageData.length - 1)) {
                    currentPage = pageData.length - 1;
                }
                setData(pageData[currentPage]);
            })
            $('.redar_box').addClass('redar_animate');

        }

        if (minSM.length) {
            // shareData.title = '【我的专属时光唱片是《' + minSM[3] + '》，点开测测你的是什么】'
            shareData.title = customShareTitle(minSM[3]);
            shareData.img = 'https://puui.qpic.cn/vupload/0/20181205_' + minSM[6] + '_' + minSM[4] + '.jpg/0';
            // this.console.log(shareData.img);
            MShare.init();
        }

    }

})();