$('.again').click(function(){
    location.href = location.href;
})

$('.g_poster').click(function(){
    $('.poster_box').show();
    $('.share_point').hide();
    gPoster();

})

$('.close_but').click(function(){

    $('.poster_warp').hide();
});

$('.mask').click(function(e){
    $('.poster_warp').hide();
    
})

$('.share').click(function(){
    $('.poster_warp').show();
    $('.poster_box').hide();
    $('.share_point').show();
});

function gPoster(){

    if(minSM.length){
        debugger;
        audio.pause();

        audio.src = 'http://mat1.gtimg.com/news/images/static/2018/reform/w_' + minSM[6] + '_' + minSM[4] + '.mp3';
        audio.play();
    }else{
        audio.pause();
        audio.src = 'http://mat1.gtimg.com/news/images/static/2018/reform/liangliang.mp3';
        audio.play();
    }
   

    debugger;
    var sharStr = minSM.length?minSM[5]:liangliang.title;
    var songSingerMess = minSM[2]||liangliang.mess;
    var songName = minSM.length?'《'+minSM[3]+'》':liangliang.name;
    var posterUrl = minSM.length?'https://puui.qpic.cn/vupload/0/20181205_'+minSM[6]+'_'+minSM[4]+'.jpg/0':liangliang.poster;
    // debugger;
    // 高级绘制
    canvas_x.makeImage({
        type: 'url',
        parts: [{
                type: 'image',
                url: 'https://puui.qpic.cn/vupload/0/20181203_1543832523447_28lv9qnkldq.jpeg/0',
                width: 750,
                height: 1334
            },{
                type: 'image',
                url: posterUrl,
                width: 500,
                height: 500,
                x:125,
                y:493
            },
            // {
            //     type: 'image',
            //     url: './assets/avatar.jpg',
            //     width: 150,
            //     height: 150,
            //     radius: 1,
            //     padding: 5,
            //     background: '#fff',
            //     x: 75,
            //     y: 20
            // },
            {
                type: 'qrcode',
                text: window.location.href,
                x: 40,
                y: 1077,
                width: 157,
                height: 157,
                padding: 2,
                background: '#fff',
                level: 3
            },
            {
                type: 'text',
                text: sharStr.slice(0,14),
                x: 224,
                y: 1077,
                color: '#abcdef',
                width:10,
                size: '34px',
                color:'#eeedde',
                bold: true
            },
            {
                type: 'text',
                text: sharStr.slice(14),
                x: 224,
                y: 1141,
                color: '#abcdef',
                width:10,
                size: '34px',
                color:'#eeedde',
                bold: true
            },
            {
                type: 'text',
                text: songSingerMess,
                x: 375,
                y: 310,
                color: '#abcdef',
                width:10,
                size: '28px',
                color:'#eeedde',
                align:'center'
            },{
                type: 'text',
                text: songName,
                x: 375,
                y: 346,
                color: '#abcdef',
                width:10,
                size: '28px',
                color:'#eeedde',
                align:'center'
            }
        ],
        width: 750,
        height: 1334
    }, (err, data) => {
        // document.getElementById('test').src = data
        $('.poster_warp .poster_img').attr('src',data);
        $('.poster_warp').show();
        // document.getElementById('down').href = data
    })
}

